import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { GamerShooterPageComponent } from './shooter-page.component';

const routes: Routes = [
  {
    path: '',
    component: GamerShooterPageComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GamerShooterPageRoutingModule { }
